  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }

  deleteRecord2(item : any, no) : void
  {
    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {
              
          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'asignapcbs').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
            }, (err) => {
              this.msj.showError3('Error',err.error);
          });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]  
  });
  alert.present();

   
  
  }

  imprime(item : any, no) : void
  {
 this.regService.getReg(item._id,'asignaestudiantes').then((data) => {
    if(item.tipoasignacion== 'enlinea')
    {
      this.Pdfx.daasigna(item._id,'si',data,item.no_orientacion,'UNIDAD ACADÉMICA: '+item.idunidadacademica.nombre ,'UBICACIÓN: '+item.idtipounidad.nombre,item.fechaini)   
    }
    else
    {
      this.Pdfx.daasigna(item._id,'si',data,item.no_orientacion,'ESTUDIARA EN: '+item.idtipounidad2.nombre + ' -> '+ item.idunidadacademica2.nombre,'REALIZARÁ EL EXÁMEN EN:' +item.idtipounidad.nombre,item.fechaini)   
    }
      
    }, (err) => {   

      this.msj.showError3('Resultado','Resultado no encontrado.');
  
   
  });
  }

  deleteRecord(item : any, no) : void
